.chat {
  display: grid;
  grid-template-columns: 25% 75%;
  grid-template-rows: 10% 80% 10%;
  flex: 1 1 auto;
  @media screen and (min-width: 992px) {
    margin: 0 35px;
  }

  .chats {
    grid-area: 2/1/4/2;
  }
  .messages {
    grid-area: 2/2/3/3;
  }

  .translation {
    grid-area: 1/2/2/3;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  .message-input {
    grid-area: 3/2/4/3;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
  }
}
